import { Component, OnInit,Output,Input,EventEmitter,ViewChild, ChangeDetectorRef} from '@angular/core';
import {FormGroup,FormControl,FormBuilder,Validators } from '@angular/forms';
import {StoreService,Store,ErrorHandleService, EstateService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';

@Component({
  selector: 'app-store-edit',
  templateUrl: './store-edit.component.html',
  styleUrls: ['./store-edit.component.scss']
})
export class StoreEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Input('StoreEditData') StoreEditData;
  @ViewChild('estateCombo') estateCombo: jqxComboBoxComponent;

  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  storeForm:FormGroup;
  sData:Store;
  estateAdapter:any = [];

  constructor( private fb:FormBuilder,
    private ehs: ErrorHandleService,
    private es: EstateService,
    private sService: StoreService,
    private cdr: ChangeDetectorRef
  ) {
    this.createForm();
    }
    createForm() {
      this.storeForm = this.fb.group({
            'code': [null,Validators.required],
            'name': [null,Validators.required],
            'address': [null,Validators.required],
            'contact_no': [null,Validators.required],
            'store_keeper': [null,Validators.required]
            });
        }

  ngOnInit() {
    this.sData=new Store(this.StoreEditData);
    this.storeForm.setValue(this.sData);
  }

  ngAfterViewInit(){

    this.es.index({limit:'all'}).subscribe(
      result => {
        console.log(result);
        this.estateAdapter = result;
        if(this.StoreEditData && this.StoreEditData.estate_id){
        console.log(this.StoreEditData);
          
          setTimeout(() => {
            this.estateCombo.selectItem(this.StoreEditData.estate_id);
            this.cdr.detectChanges();
          }, 500);
        }
      },
      error => {
        console.log(error);
      }
    );

  }
  saveBtn(post){ 
    this.jqxLoader.open();
    this.sService.update(this.StoreEditData.id,post).subscribe(
      result => {
        let data = this.ehs.formatError(result);
        if (data['result'] == true) {
          this.UpdatedData.emit(data);
        } else {
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerHTML = '';
          for (let i = 0; i < data['message'].length; i++) {
            let errorDiv = document.createElement('li');
            errorDiv.innerText = data['message'][i];
            messageDiv.appendChild(errorDiv);
          }
          this.winNotification.open();
        }
        this.jqxLoader.close();
      },
      error => {
        let data = this.ehs.formatError(error);
        let messageDiv: any = document.getElementById('windowMessage');
        messageDiv.innerText = data['message'];
        this.winNotification.open();
        this.jqxLoader.close();
    });
  }

  cancelBtn(){
    this.cancelData.emit(true);
  }
  rules =
  [
      { input: '.code', message: 'Code required !', action: 'keyup, blur', rule: 'required' },
      { input: '.name', message: 'Name required!', action: 'keyup, blur', rule: 'required' },
      { input: '.address', message: 'Address required!', action: 'keyup, blur', rule: 'required' },
      { input: '.contact_no', message: 'Contact no required!', action: 'keyup, blur', rule: 'required' } 
      
  ];        

}



